<template>
<div>
  <div class="alert alert-danger alert-dismissible fade show" role="alert">
    <button type="button" class="close" data-dismiss="alert" aria-label="Close">
      <span aria-hidden="true">&times;</span>
    </button>
    <strong>Other runners will be here as soon as possible. Until then, run your self and practice your typing speed here</strong>
  </div>

  <RacingPanel
    :racing="racing"
    :timer="timer"
    :racers="racers"
    :me="me"
    @count-down-done="startRacing"></RacingPanel>
  <QuoteBox
    :text="quoteText"
    :typed="typed"
    :current-word="currentWord"
    :current-word-typed="currentWordTyping"></QuoteBox>
  <TypeBox
    v-if="!typingDone"
    :lock="inputLock"
    :word="currentWordTyping"
    @input="updateRacing"></TypeBox>
</div>
</template>

<script>
import QuoteBox from '@/components/QuoteBox.vue'
import TypeBox from '@/components/TypeBox.vue'
import RacingPanel from '@/components/for-racing/RacingPanel'
import {
  mapActions,
  mapState
} from 'vuex'

export default {
  components: {
    QuoteBox,
    TypeBox,
    RacingPanel
  },

  data () {
    return {
      currentWord: '',
      currentWordTyping: '',
      inputLock: true,
      timer: 3,
      typed: '',
      quoteText: 'Waiting your text',
      wordRegex: /[^ ]* ?/,
      wordsTyped: 0,
      me: {
        name: 'Me',
        username: 'me',
        progress: 0,
        wpm: 0,
        wpmHistory: []
      },
      racers: [{
        name: 'Friend',
        username: 'friend',
        progress: 30
      },
      {
        name: 'Friend1',
        username: 'friend',
        progress: 0
      },
      {
        name: 'Friend2',
        username: 'friend',
        progress: 100
      },
      {
        name: 'Friend3',
        username: 'friend',
        progress: 70
      }
      ],
      racing: {
        loading: true,
        started: false,
        startedAt: 0,
        lastWordStartedAt: 0
      }
    }
  },

  created () {
    this.getRacing().then(() => {
      this.quoteText = this.sRacing.quote.text
      this.racing.loading = false
    })
  },

  computed: {
    ...mapState({
      sRacing: state => state.racing.racing
    }),
    typingDone () {
      return this.quoteText === this.typed
    }
  },

  methods: {
    ...mapActions('racing', {
      getRacing: 'getRacing'
    }),

    calculateProgress () {
      this.me.progress = (this.typed.length * 100) / this.quoteText.length
    },

    incrementTypedWord () {
      this.wordsTyped += 1
    },

    getNextWord () {
      let tail = this.quoteText.slice(this.typed.length)
      let next = tail.match(this.wordRegex)[0]
      return next
    },

    getPartialWPM () {
      return ((this.lastWordTyped.length - 1) / (this.timeDeltaSec(Date.now(), this.racing.lastWordStartedAt))) * 60 * (1 / 5)
    },

    getWPM () {
      return ((this.typed.length - 1) / (this.timeDeltaSec(Date.now(), this.racing.startedAt))) * 60 * (1 / 5)
    },

    registerCurrentVelocity () {
      this.me.wpm = this.getWPM()
      this.me.wpmHistory.push(this.getPartialWPM())
    },

    startRacing () {
      this.racing.started = true
      this.startTimer()
      this.startTyping()
    },

    startTimer () {
      let now = Date.now()
      this.racing.startedAt = now
      this.racing.lastWordStartedAt = now
    },

    startTyping () {
      this.currentWord = this.getNextWord()
      this.inputLock = false
    },

    timeDeltaSec (after, before) {
      return (after - before) / 1000
    },

    updateCurrentWord (e) {
      if (e === this.currentWord) {
        this.typed += this.currentWord
        this.lastWordTyped = this.currentWord
        this.currentWord = this.getNextWord()
        this.currentWordTyping = ''
        return true
      } else {
        this.currentWordTyping = e
        return false
      }
    },

    updateRacing (e) {
      let isNewWord = this.updateCurrentWord(e)
      if (isNewWord) {
        this.incrementTypedWord()
        this.registerCurrentVelocity()
        this.calculateProgress()
        this.racing.lastWordStartedAt = Date.now()
        this.$emit('updateRacing', this.me)
      }
    }

  }

}
</script>

<style lang="scss" scoped>
.carbon {
  width: 300px;
  height: 300px;
}
</style>
