<template>
<div class="racing-panel">
  <RacingTracker
    :racers="racers"
    :me="me"></RacingTracker>
  <div class="racing-bar">
    <div
      v-if="racing.loading"
      class="loadin-flag">
      <div>Loading text</div>
      <img class="loadin-flag__img" alt="Vue logo" src="@/assets/flag.gif">
    </div>
    <CountDown
      v-else-if="!racing.started"
      :time="timer"
      @done="countDownDone"></CountDown>
    <SpeedometerDigital
      v-else
      :velocity="me.wpm"
      :velocity-last-word="velocityLastWord"></SpeedometerDigital>
  </div>
</div>
</template>

<script>
import CountDown from '@/components/for-racing/CountDown.vue'
import RacingTracker from '@/components/RacingTracker.vue'
import SpeedometerDigital from '@/components/for-racing/SpeedometerDigital.vue'

export default {
  name: 'RacingPanel',

  components: {
    CountDown,
    RacingTracker,
    SpeedometerDigital
  },

  props: {
    racing: {
      type: Object,
      required: true
    },
    timer: {
      type: Number,
      required: true,
      default: 0
    },
    me: {
      type: Object,
      required: true,
      default: () => {}
    },
    racers: {
      type: Array,
      required: true
    }
  },

  computed: {
    velocityLastWord () {
      return this.me.wpmHistory.slice(-1)[0] || 0
    }
  },

  methods: {
    countDownDone () {
      this.$emit('count-down-done')
    }
  }
}
</script>

